import React, { useEffect, useState } from 'react'

const Useeffect = () => {
  useEffect(()=>{
    alert("we are using useeffect")
  },[])

const[clicked,setclicked]=useState();

   
  return (
    <div> 
      <button onClick={()=>{
        setclicked('subscribe')
      }}>subscribe</button>
      <br></br>
      <hr></hr>
      <button onClick={()=>{
        setclicked('to')
      }}>To</button>
      <br></br>
      <hr></hr>
      <button onClick={()=>{
        setclicked("dropxout")
      }}>Dropxout</button>
     <br></br>
     clicked:{clicked}
      {console.log(clicked)}
    </div>
  )
}

export default Useeffect
